  @media (max-width: 1023px) {
    .social {
      display: block; } }
  .social a {
    margin: 0 15px; }
    @media (max-width: 479px) {
      .social a {
        margin: 0 8px; } }
  .social svg {
    width: 40px;
    height: 38px;
    fill: #fff; }
    @media (max-width: 767px) {
      .social svg {
        width: 35px;
        height: 32px; } }
    @media (max-width: 479px) {
      .social svg {
        width: 30px;
        height: 23px; } }

/* ---------------burger */
.burger {
  display: none;
  position: relative;
  width: 30px;
  height: 15px;
  z-index: 5; }
  @media (max-width: 1023px) {
    .burger {
      display: block; } }
  .burger span {
    position: absolute;
    top: 6px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    border-radius: 10px;
    transition: all .7s ease 0s; }
  .burger::before, .burger::after {
    content: '';
    position: absolute;
    right: 0;
    height: 2px;
    width: 100%;
    background-color: #fff;
    border-radius: 10px; }
  .burger::before {
    top: 0; }
  .burger::after {
    bottom: 0;
    width: 50%; }

.burger_active span {
  opacity: 0; }

.burger_active::before, .burger_active::after {
  animation-duration: .7s;
  animation-fill-mode: forwards; }

.burger_active::before {
  animation-name: burger1; }

.burger_active::after {
  animation-name: burger2; }

@keyframes burger1 {
  0% {
    top: 0px; }
  40% {
    transform: rotate(0deg);
    top: 6px; }
  100% {
    transform: rotate(45deg);
    top: 6px; } }

@keyframes burger2 {
  0% {
    bottom: 0; }
  40% {
    transform: rotate(0deg);
    bottom: 7px;
    width: 100%; }
  100% {
    transform: rotate(-45deg);
    bottom: 7px;
    width: 100%; } }

.burger_finish::before, .burger_finish::after {
  animation-duration: .7s;
  animation-fill-mode: forwards; }

.burger_finish::before {
  animation-name: burger3; }

.burger_finish::after {
  animation-name: burger4; }

@keyframes burger3 {
  0% {
    transform: rotate(45deg);
    top: 6px; }
  40% {
    transform: rotate(0deg);
    top: 6px; }
  100% {
    top: 0; } }

@keyframes burger4 {
  0% {
    transform: rotate(-45deg);
    bottom: 6px;
    width: 100%; }
  40% {
    transform: rotate(0deg);
    bottom: 6px;
    width: 100%; }
  100% {
    bottom: 0; } }

body {
  font-family: 'Poppins', sans-serif; }

.body_lock {
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 15px; }

.main-btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-width: 191px;
  padding: 13px 15px;
  border: 1px solid #fff;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  z-index: 1;
  overflow: hidden;
  transition: all .3s ease 0s; }
  .main-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0.25turn, #fc1f1f, #fc9315);
    transition: all .3s ease 0s;
    z-index: -1;
    opacity: 0; }
  .main-btn:hover {
    border: 1px solid transparent; }
    .main-btn:hover::before {
      opacity: 1; }
  .main-btn img {
    margin-left: 40px; }

/* -----------------------------main------------------------- */
.main {
  position: relative;
  padding: 90px 0;
  background-color: rgba(10, 10, 10, 0.8); }
  .main video, .main iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1; }
  .main .container {
    min-height: calc(100vh - 180px);
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media (max-width: 1023px) {
      .main .container {
        justify-content: flex-end; } }
  .main .content {
    max-width: 50%; }
    @media (max-width: 767px) {
      .main .content {
        max-width: 55%; } }
    @media (max-width: 599px) {
      .main .content {
        max-width: 100%; } }
  .main h1 {
    /* Только если меньше контейнера */
    font-size: 55px;
    margin-bottom: 30px; }
    @media (max-width: 1150px) {
      .main h1 {
        font-size: calc(24px + 31 * ((100vw - calc(320px)) / 830)); } }
  .main p {
    /* Только если меньше контейнера */
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 50px; }
    @media (max-width: 1150px) {
      .main p {
        font-size: calc(14px + 4 * ((100vw - calc(320px)) / 830)); } }
  @media (max-width: 479px) {
    .main .main-btn {
      display: block;
      max-width: 191px;
      margin: 0 auto; } }
